.banner {
  height: 729px;
  padding-top: 125px;
  text-align: center;
  background-image: url("../img/bg-banner-mobile@1x.jpg");
  background-color: $background-color-tertiary;
  background-repeat: no-repeat;
  background-size: cover;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-banner-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    padding-top: 228px;
    background-image: url("../img/bg-banner-tablet@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-banner-tablet@2x.jpg");
    }

    .container .banner__button:last-child {
      margin-right: 0;
    }
  }

  @media (min-width: $desktop-width) {
    padding-top: 167px;
    background-image: url("../img/bg-banner-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-banner-desktop@2x.jpg");
    }
  }

  .title {
    text-transform: none;
    color: $color-primary;
    font-size: 18px;
    line-height: 30px;
    font-weight: 400;
    text-align: center;
    margin: 25px auto 40px;
    padding-bottom: 0;
    padding-left: 20px;
    padding-right: 20px;

    @media (min-width: $tablet-width) {
      font-size: 22px;
      margin: 39px auto 42px;
      max-width: 450px;
      padding-left: 35px;
      padding-right: 35px;
    }
  }
}
.banner__image {
  display: block;
  margin: 0 auto;
  width: 260px;

  @media (min-width: $tablet-width) {
    @include size(462px, 69px);
  }
}

.banner__button {
  @include size(283px, 60px);
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    width: 310px;
    margin-right: 10px;
  }
}

.banner__button-text {
  text-transform: uppercase;
  color: $color-primary;
  font-size: 16px;
  font-weight: 700;
}
