.contacts {
  margin-top: 73px;

  @media (min-width: $tablet-width) {
    margin-top: 97px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 71px;
  }

  .title {
    //padding: 0 0 16px;
    //text-align: center;

    //@media (min-width: $tablet-width) {
    //  padding: 0 0 81px;
    //}

    //@media (min-width: $desktop-width) {
    //  padding: 0 0 107px;
    //}
  }
}

.contacts__header {
  display: block;
  font-weight: 700;

  &--inn {
    display: inline-block;
  }

  &--email {
    display: inline-block;
  }
}

.contacts__tel {
  display: block;
  text-decoration: none;
  color: $color-secondary;
}

.contacts__mail {
  color: $color-tertiary;
}

.contacts__wrapper {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row-reverse;
    justify-content: space-between;
  }
}

.contacts__text {
  padding: 0 20px;
  margin-bottom: 40px;
  z-index: 2;

  &-company {
    margin-bottom: 0;
    margin-top: 0;
  }

  &-legaladdress {
    margin-top: 10px;
    margin-bottom: 0;
    max-width: 192px;
  }

  &-realaddress {
    margin-top: 10px;
    margin-bottom: 0;
    max-width: 192px;
  }

  &-inn {
    margin-top: 10px;
    margin-bottom: 0;
  }

  &-okpo {
    margin-top: 10px;
    margin-bottom: 0;
  }

  &-phones {
    margin-top: 10px;
    margin-bottom: 0;
  }

  &-email {
    margin-top: 10px;
    margin-bottom: 0;

    @media (min-width: $tablet-width) {
      max-width: 190px;
    }
  }

  @media (min-width: $tablet-width) {
    width: 220px;
    padding: 0;
  }
}

.contacts__map {
  position: relative;
  overflow: hidden;
}

.contacts__map-iframe {
  position: absolute;
  display: block;
  top: 0;
  left: 0;
  height: 100%;
  border: 0;
  filter: grayscale(100%);
  filter: grayscale(1); /* Firefox 4+ */
  filter: gray; /* IE 6-9 */
}


.contacts__map-image {
  @media (min-width: $tablet-width) {
    width: 420px;
  }

  @media (min-width: $desktop-width) {
    width: 630px;
  }
}

.contacts__container {
  padding: 0;

  @media (min-width: $tablet-width) {
    padding: 0 10px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 111px;
    padding-right: 99px;
  }
}


