.sign {
  display: flex;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    align-items: center;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 82px;
  }
}

.sign__text {
  font-size: 12px;
  width: 155px;

  @media (min-width: $tablet-width) {
    width: 270px;
    font-size: 14px;
  }
}

.sign__img {
  width: 92px;
  height: 69px;
  object-fit: contain;

  @media (min-width: $tablet-width) {
    width: 181px;
    height: 115px;
  }
}

.sign__stamp {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row-reverse;
    justify-content: center;
    align-items: center;
    width: 290px;
  }

  p {
    font-size: 12px;

    @media (min-width: $tablet-width) {
      font-size: 14px;
    }
  }
}
