.news {
  margin-top: 62px;

  @media (min-width: $tablet-width) {
    margin-top: 48px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 70px;
  }

  .title {
    text-align: left;

    @media (min-width: $tablet-width) {
      font-size: 24px;
      margin-bottom: 30px;
    }
  }
}


.news--inner {
  margin-top: 76px;

  @media (min-width: $tablet-width) {
    margin-top: 93px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 69px;
  }

  .title {
    padding: 0;

    @media (min-width: $tablet-width) {
      font-size: 32px;
      line-height: 43px;
    }

    @media (min-width: $desktop-width) {
      //padding: 0 0 103px;
      //font-size: 32px;
      //line-height: 43px;
    }
  }
}

.news__container {

  @media (min-width: $tablet-width) {
    padding: 0 10px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 111px;
    padding-right: 109px;
  }
}

.news__list {
  @include ul-reset;
  display: flex;
  flex-direction: column;
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
    margin-bottom: 46px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 0;
  }
}

.news__item {
  display: flex;
  flex-direction: column;
  position: relative;
  width: 100%;
  margin-bottom: 35px;

  @media (min-width: $tablet-width) {
    flex: 0 0 48%;
    max-width: 48%;
  }

  @media (min-width: $desktop-width) {
    flex: 0 0 32%;
    max-width: 32%;
  }
}

.news__img {
  order: 1;
  height: 180px;
  object-fit: cover;

  @media (min-width: $tablet-width) {
    //width: 280px;
    //height: 180px;
  }
}

.news__header {
  order: 2;
  font-size: 16px;
  line-height: 20px;
  margin-top: 20px;
  margin-bottom: 12px;
}

.news__preview {
  order: 3;
  font-size: 12px;
  line-height: 20px;
  margin: 0;
}

.news__data {
  position: absolute;
  top: 0;
  left: 0;
  font-size: 12px;
  line-height: 24px;
  color: $color-primary;
  padding: 1px 7px;
  background-image: url("../img/bg-data.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  width: 90px;
  height: 26px;
}

.news__pagination {
  margin-bottom: 8px;
}
