.download {
  justify-content: space-between;
}

.download__button {
  width: 100%;
  height: 35px;

  @media (min-width: $tablet-width) {
    width: 207px;
    margin-top: 14px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 0;
  }
}

.download__button-text{
  color: $color-primary;
  display: flex;
  align-items: center;

  @media (min-width: $desktop-width) {
    width: 173px;
  }
}

.download__text {
  margin-top: 5px;
  margin-bottom: 17px;

  @media (min-width: $tablet-width) {
    width: 430px;
  }

  @media (min-width: $desktop-width) {
    width: 394px;
    margin-top: 0;
  }
}
