.page-footer {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 22px auto 0;

  @media (min-width: $tablet-width) {
    margin: 53px auto 0;
  }

  @media (min-width: $desktop-width) {
    margin: 43px auto 0;
  }
}

.page-footer__nav {
  @include visually-hidden;

  @media (min-width: $tablet-width) {
    position: relative;
    width: 100%;
    height: auto;
    margin: 0;
    border: 0;
    padding: 0;
    clip: unset;
    display: flex;
    justify-content: center;
  }
}

.page-footer__container {
  display: flex;
  flex-direction: column;
  align-items: center;

  @media (min-width: $tablet-width) {
    padding: 0 10px;
  }
}

.page-footer__nav-list {
  @media (min-width: $tablet-width) {
    width: 100%;
  }
}

.page-footer__logo {
  margin-top: 9px;
}

.page-footer__nav-list {
  @media (min-width: $tablet-width) {
    justify-content: space-between;
  }
}

.page-footer__copyright {
  @media (min-width: $tablet-width) {
    margin-top: 6px;
  }
}
