* {
  box-sizing: border-box;
}

body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  font-family: $font-family-primary;
  font-size: 14px;
  line-height: 20px;
  font-weight: 400;
  font-style: normal;
  background: $background-color-fourth;
  overflow-x: hidden;
  color: $color-secondary;
}

img {
  max-width: 100%;
  height: auto;
}

.visually-hidden {
  @include visually-hidden;
}

.container {
  display: block;
  //width: 320px;
  width: 100%;
  padding: 0 20px;
  margin: 0 auto;

  @media (min-width: $tablet-width) {
    width: 708px;
    padding: 0 20px;
  }

  @media (min-width: $desktop-width) {
    width: 1120px;
    padding: 0 20px;
    margin: 0 auto;
  }
}


