.main-nav__toggle {
  @include size(30px, 25px);
  box-sizing: content-box;
  position: absolute;
  top: 48px;
  right: 30px;
  z-index: 2;
  display: block;
  background-color: transparent;
  cursor: pointer;
  border: none;
  font-size: 0;
  padding: 0;

  &:hover,
  &:active {
    background-color: transparent;
  }

  @media (min-width: $tablet-width) {
    display: none;
  }
}

.main-nav--closed .main-nav__toggle::before {
  @include pseudo;
  top: 0;
  left: 0;
  margin-left: 0;
  margin-top: 0;

  @include size(30px, 5px);
  background-color: #ffffff;
  box-shadow: 0 10px 0 0 #ffffff, 0 20px 0 0 #ffffff;
}

.main-nav--closed .main-nav__toggle:active::before {
  background-color: rgba(255, 255, 255, 0.3);
  box-shadow: 0 10px 0 0 rgba(255, 255, 255, 0.3), 0 20px 0 0 rgba(255, 255, 255, 0.3);
}

.main-nav--closed .main-nav__list {
  display: none;

  @media (min-width: $tablet-width) {
    display: flex;
  }
}

.main-nav--opened .main-nav__toggle {
  top: 45px;

  &::before,
  &::after {
    @include pseudo;
    right: 0;

    @include size(30px, 5px);
    background-color: #fff;
  }

  &::before {
    transform: rotate(45deg);
    box-shadow: none;
  }

  &::after {
    transform: rotate(-45deg);
  }

  &:active::before,
  &:active::after {
    background-color: rgba(255, 255, 255, 0.3);
  }
}

.main-nav--opened {
  background-color: $background-color-primary;
  width: 100%;
  height: 100vh;
  padding: 40px 20px 0;
  z-index: 999;
  background-image: url("../img/bg-menu-mobile@1x.jpg");
  background-repeat: no-repeat;
  background-size: contain;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-menu-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    background-image: none;
    background-color: transparent;
  }

  .page-header__contacts {
    width: auto;
    height: auto;
    margin: 33px 0 0 0;
    clip: unset;
    position: relative;
    text-align: center;
  }

  .mail {
    color: $color-primary;
  }

  .page-header__copyright {
    width: auto;
    height: auto;
    margin: 13px 0 0 0;
    clip: unset;
    position: relative;
  }

  .main-nav__list {
    display: flex;
    text-align: center;
  }

}


.main-nav {
  width: 100%;

  @media (min-width: $desktop-width) {
    width: 694px;
    margin: auto;
  }
}

.main-nav__list {
  @include ul-reset;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-evenly;
  }

  @media (min-width: $desktop-width) {
    max-width: 694px;
    margin: auto;
  }
}

.main-nav__link {
  text-decoration: none;
  text-transform: uppercase;
  font-size: 14px;
  line-height: 38px;
  font-weight: 700;
  position: relative;
  color: #fff;

  &::before {
    @include pseudo;
    bottom: -4px;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #fff;
    display: block;
    transition: all 0.2s;
    opacity: 0;
  }

  &:hover::before,
  &:active::before,
  &:focus::before {
    opacity: 1;
  }
}

.main-nav__item--active {
  .main-nav__link::before {
    opacity: 1;
  }
}

.main-nav__link--header {
  color: $color-primary;
}

.main-nav__link--footer {
  color: $color-secondary;
}
