
.page-header {
  color: #fff;
  flex: 0 0 auto;
  position: absolute;
  width: 100%;
  padding-bottom: 4px;

  @media (min-width: $tablet-width) {
    padding-bottom: 16px;
  }

  @media (min-width: $desktop-width) {
    padding-bottom: 30px;
  }
}

.page-header__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 32px 0 0;
  width: 100%;
  position: relative;

  @media (min-width: $tablet-width) {
    width: 708px;
  }

  @media (min-width: $desktop-width) {
    width: 1120px;
    margin: 0 auto;
    flex-direction: row;
    padding: 30px 20px 0;
  }
}

.page-header__logo {
  margin-bottom: 20px;
  z-index: 9999;

  @media (min-width: $tablet-width) {
    margin-bottom: 19px;
  }
  @media (min-width: $tablet-width) {
    margin-bottom: 0;
  }
}


.page-header__tel {
  display: flex;
  flex-direction: column;
  position: absolute;
  left: 30px;
  top: 45px;
  z-index: 9999;

  @media (min-width: $tablet-width) {
    @include visually-hidden;
  }
}

.page-header__contacts {
  @include visually-hidden;

  @media (min-width: $tablet-width) {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    right: 0;
    margin-top: 8px;
    margin-right: 20px;
    width: auto;
    height: auto;
    clip: unset;
  }

  @media (min-width: $desktop-width) {
    margin-top: -3px;
  }

}

.page-header__copyright {
  @include visually-hidden;
}

.page-header__nav {
  width: 100%;

  @media (min-width: $tablet-width) {
    margin-top: 17px;
  }

  @media (min-width: $desktop-width) {
    width: 696px;
    margin-left: 85px;
    margin-top: 0;
  }
}

.page-header--inner {
  position: relative;
  background-image: url("../img/bg-headerinner-mobile@1x.jpg");
  background-color: $background-color-tertiary;
  background-repeat: no-repeat;
  background-size: cover;
  height: 120px;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-headerinner-mobile@2x.jpg");
  }

  @media (min-width: $tablet-width) {
    height: 170px;
    background-image: url("../img/bg-headerinner-tablet@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-headerinner-tablet@2x.jpg");
    }
  }

  @media (min-width: $desktop-width) {
    height: 125px;
    background-image: url("../img/bg-headerinner-desktop@1x.jpg");

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-headerinner-desktop@2x.jpg");
    }
  }
}
