.map {
  background-image: url("../img/bg-map-mobile@1x.png");
  background-repeat: no-repeat;
  height: 481px;
  background-size: contain;
  background-position-y: bottom;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-map-mobile@2x.png");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/bg-map-tablet@1x.png");
    background-size: 100%;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-tablet@2x.png");
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/bg-map-desktop@1x.png");
    background-size: 1080px 677px;
    background-position: center;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-map-desktop@2x.png");
    }
  }

  .title {
    padding-left: 0;
    padding-bottom: 0;
    padding-top: 73px;
    max-width: 178px;
    font-weight: 700;
    font-size: 24px;
    line-height: 30px;
    margin-bottom: 12px;
  }
}

.map__container {
  position: relative;
  height: 481px;
}

.map__wrapper {
  background-image: url("../img/bg-routes-mobile.svg");
  position: absolute;
  top: -63px;
  left: 0;
  height: 419px;
  background-repeat: no-repeat;
  background-size: 100% 419px;
  z-index: 2;
  width: 100%;
  padding: 0 20px;

  @media (min-width: $tablet-width) {
    background-image: url("../img/bg-routes-tablet.svg");
    height: 419px;
    background-size: 525px 419px;
    left: -33px;
    top: -184px;
    padding-left: 40px;
    width: 525px;

    background-image: url(../img/bg-routes-tablet.svg);
    height: 419px;
    background-size: 493px 419px;
    left: -33px;
    top: -184px;
    padding-left: 40px;
    width: 493px;
  }

  @media (min-width: $desktop-width) {
    left: -15px;
    background-image: url("../img/bg-routes-desktop.svg");
    top: -236px;
    padding-left: 133px;
    background-size: 616px 420px;
    width: 616px;
  }
}

.map__description {
  max-width: 268px;

  @media (min-width: $tablet-width) {
    max-width: 320px;
  }

  p {
    margin-bottom: 2px;
  }

  ul {
    padding-left: 20px;
    margin-top: 0;
  }
}


