.price {
  margin-top: 72px;

  @media (min-width: $tablet-width) {
    margin-top: 95px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 68px;
  }

  .title {
    //padding: 0 0 42px;
    //text-align: center;

    //@media (min-width: $desktop-width) {
    //  padding: 0 0 94px;
    //}
  }
}

.price__container {

  @media (min-width: $tablet-width) {
    padding: 0 10px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 111px;
    padding-right: 99px;
  }
}

.price__wrapper {
  @media (min-width: $desktop-width) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: flex-start;
  }
}

.price__header {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
  }
}

.price__table {
  border-collapse: collapse;
  width: 100%;
  margin: 20px 0;

  @media (min-width: $tablet-width) {
    margin-top: 5px;
    margin-bottom: 47px;
  }

  thead {
    display: none;

    td {
      @media (min-width: $tablet-width) {
        padding: 7px 10px;
      }
    }

    span {
      font-weight: 700;
      display: block;
    }

    @media (min-width: $tablet-width) {
      display: table-header-group;
      background: linear-gradient(333.94deg, #2D2E83 12.73%, #4A84C5 100.06%);
      color: $color-primary;

    }
  }

  tr {
    display: block;

    &:nth-child(even) {
      background-color: #F4F4F4;

      td {
        @media (min-width: $tablet-width) {
          padding: 5px 10px;
        }
      }
    }

    @media (min-width: $tablet-width) {
      display: table-row;
    }
  }

  th {
    padding: 10px;
  }

  td {
    display: flex;
    justify-content: space-between;
    font-size: 12px;
    padding: 10px;
    border: 0;

    @media (min-width: $tablet-width) {
      display: table-cell;
      padding: 12px 10px;
    }

    &::before {
      content: attr(data-label);
      margin-right: 0;
      width: 100px;

      @media (min-width: $tablet-width) {
        @include visually-hidden;
      }
    }
  }
}


.price__table-center {

  @media (min-width: $tablet-width) {
    text-align: center;
  }
}

.price__social {
  @media (min-width: $tablet-width) {
    flex-direction: row;
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;
  }

  .social__header {
    @media (min-width: $tablet-width) {
      margin-right: 30px;
    }

    @media (min-width: $desktop-width) {
      margin-right: 0;
    }
  }
}
