.projects {
  margin-top: 73px;

  @media (min-width: $tablet-width) {
    margin-top: 92px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 69px;
  }

  .title {
    //padding: 0 0 42px;
    //text-align: center;

    //@media (min-width: $tablet-width) {
    //  padding: 0 0 105px;
    //}
  }
}

.projects__container {
  @media (min-width: $tablet-width) {
    padding: 0 10px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 111px;
    padding-right: 99px;
  }
}

.projects__title {
  //max-width: 190px;
  //margin: 0 auto;

  //@media (min-width: $tablet-width) {
  //  max-width: 300px;
  //}
}

.projects__list {
  @include ul-reset;
}

.projects__item {
  display: flex;
  flex-direction: column;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
  }
}

.projects__header {
  font-size: 20px;
  line-height: 26px;
  margin-right: auto;
  margin-top: 0;

  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right,#4a84c5,#2d2e83);

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ specific styles go here */
    color: #2d2e83;
    background-color: transparent;
    background-image: none;
  }

  @media (min-width: $tablet-width) {
    max-width: 150px;
    margin-top: 0;
  }
}

.projects__advantages {
  margin-top: 19px;
  margin-bottom: 22px;
  padding-left: 10px;
  font-size: 16px;
  line-height: 30px;
  font-weight: 700;
  //width: 449px;

  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right,#4a84c5,#2d2e83);

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ specific styles go here */
    color: #2d2e83;
    background-color: transparent;
    background-image: none;
  }

  li {
    list-style-type: '- ';
  }

  @media (min-width: $desktop-width) {
    width: 70%;
  }
}

.projects__description {

  @media (min-width: $tablet-width) {
    width: 449px;
  }

  p {
    margin: 5px 0;
  }

  @media (min-width: $desktop-width) {
    width: 620px;
  }
}

.projects__gallery {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;

  @media (min-width: $tablet-width) {
    width: 449px;
  }

  @media (min-width: $desktop-width) {
    width: 620px;
    flex-direction: row;
  }
}

.projects__img {
  height: 225px;
  width: 100%;
  object-fit: cover;
  display: block;
  margin-bottom: 20px;

  @media (min-width: $tablet-width) {
    height: 345px;
    margin-bottom: 31px;
  }

  @media (min-width: $desktop-width) {
    width: 300px;
    height: 225px;
    display: inline-block;
    margin-right: 20px;

    &:nth-child(even) {
      margin-right: 0;
    }
  }

}
