.pagination {
  display: flex;
  justify-content: center;
}

.pagination__link {
  text-decoration: none;
  transition: all .3s;
  width: 40px;
  height: 35px;
  text-align: center;
  transform: skew(-20deg);
  margin: 0 4px;

  display: flex;
  justify-content: center;
  align-items: center;

  border: 1px solid #2d2e83;
  border-image: linear-gradient(to right, #4a84c5, #2d2e83);
  border-image-slice: 1;


}

.pagination__link.active {
  background: linear-gradient(333.94deg, #2D2E83 12.73%, #4A84C5 100.06%);

  .pagination__text {
    color: $color-primary;
  }
}

.pagination a:hover:not(.active) {
  background: linear-gradient(333.94deg, #2D2E83 12.73%, #4A84C5 100.06%);

  .pagination__text {
    color: $color-primary;
  }
}

.pagination__text {
  transform: skewX(20deg);
  display: inline-block;

  color: transparent;
  background-clip: text;
  background-image: linear-gradient(to right, #4a84c5, #2d2e83);

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ specific styles go here */
    color: #2d2e83;
    background-color: transparent;
    background-image: none;
  }
}
