.services {
  margin-top: 72px;

  @media (min-width: $tablet-width) {
    margin-top: 92px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 69px;
  }

  //.title {
  //  padding: 0 0 42px;
  //  text-align: center;
  //}
}

.services__container {
  @media (min-width: $tablet-width) {
    width: 100%;
    padding: 0;
  }

  @media (min-width: $desktop-width) {
    width: 1120px;
    padding: 0 99px;
  }
}

.services__list {
  @include ul-reset;
}

.services__item {
  display: flex;
  flex-direction: column;
  margin-left: -20px;
  margin-right: -20px;
  padding: 0 20px 33px;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    padding-left: 0;
    padding-right: 20px;
    margin-left: 0;
    margin-right: 0;
  }
}
.services__item:not(:first-child) {
  padding: 30px 20px 33px;
}

.services__item:not(:last-child) {
  border-bottom: 1px solid rgba(72, 125, 192, 0.3);
}

.services__header {
  font-size: 20px;
  line-height: 26px;
  order: 2;
  max-width: 175px;
  margin-top: 10px;
  margin-bottom: 19px;

  color: transparent;
  background-clip: text;
  background-image: linear-gradient(to right, #4a84c5, #2d2e83);

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ specific styles go here */
    color: #2d2e83;
    background-color: transparent;
    background-image: none;
  }

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}

.services__wrapper {
  background-image: url("../img/service-mask.svg");
  width: 227px;
  height: 143px;
  margin: 0 auto;
  display: flex;

  @media (min-width: $tablet-width) {
    margin-right: 39px;
    margin-left: -55px;
    margin-top: -13px;
  }

  @media (min-width: $desktop-width) {
    margin-left: 0;
  }
}

.services__img {
  order: 1;
  margin: auto;
  margin-left: 106px;
  margin-top: 32px;


  @media (min-width: $tablet-width) {
    margin-left: 106px;
  }
}

.services__description {
  order: 3;

  ol, ul {
    padding-left: 15px;

    li {
      margin: 7px 0;
    }

    ul {
      padding-left: 15px;
    }
  }

  @media (min-width: $tablet-width) {
    max-width: 287px;
    margin-left: auto;
  }

  @media (min-width: $desktop-width) {
    max-width: 350px;
  }

}
