.calc {
  background-color: #EC8931;
  padding: 20px 26px;
  margin-top: 49px;
  margin-bottom: 56px;

  @media (min-width: $tablet-width) {
    padding: 20px;
    margin-bottom: 40px;
    margin-top: 64px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 0;
    width: 210px;
  }
}

.calc__header {
  font-size: 16px;
  line-height: 20px;
  font-weight: bold;
  color: $color-primary;
}

.calc__list {
  @include ul-reset;
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    justify-content: space-between;

    .calc__group:first-child {
      width: 170px;
    }

    .calc__group:nth-child(2) {
      width: 240px;
    }

    .calc__group:last-child {
      width: 170px;
    }
  }

  @media (min-width: $desktop-width) {
    flex-direction: column;

    .calc__group:first-child {
      width: 100%;
    }

    .calc__group:nth-child(2) {
      width: 100%;
    }

    .calc__group:last-child {
      width: 100%;
    }
  }
}

.calc__group {
  position: relative;
}

.calc__link {
  color: $color-primary;
}

.calc__item {
  margin-top: 20px;

  &--checkbox {
    display: flex;

    @media (min-width: $tablet-width) {
      position: absolute;
      top: -30px;
    }

    @media (min-width: $desktop-width) {
      position: relative;
      top: 0;
    }
  }
}

.calc__label {
  @include visually-hidden;

  &--checkbox {
    @include size(100%, auto);
    position: relative;
    margin: 0;
    border: 0;
    clip: unset;
    overflow: hidden;
    font-size: 12px;
    line-height: 12px;
    color: $color-primary;
    padding: 0 0 0 76px;

    @media (min-width: $tablet-width) {
      font-size: 10px;
      line-height: 10px;
      width: 122px;
    }
  }
}

.calc__input {
  @include size(100%, 35px);
  font-size: 12px;
  line-height: 24px;
  border: 1px solid #c16818;
  padding: 7px 13px;
  color: #C4C4C4;

  &::placeholder {
    color: #C4C4C4;
    font-size: 12px;
    line-height: 24px;
  }

  &--checkbox {
    width: 30px;
  }
}

.calc__button {
  width: 100%;
  background-color: transparent;
  padding-top: 2px;
  padding-bottom: 2px;

  @media (min-width: $tablet-width) {
    margin-top: 4px;
  }
}

.calc__button-text {
  font-size: 12px;
  line-height: 24px;
  color: $color-primary;
}

.select-hidden {
  display: none;
  visibility: hidden;
  padding-right: 10px;
}

.select {
  @include size(100%, 35px);
  cursor: pointer;
  display: inline-block;
  position: relative;
  font-size: 12px;
  line-height: 24px;
  color: $color-secondary;
  background-color: $background-color-fourth;
  border: 1px solid #C16818;
}

.select-styled {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  padding: 7px 13px;
  transition: all 0.2s ease-in;
}

.select-styled::after {
  @include size(9px, 9px);
  @include pseudo;
  background-image: url("../img/icon-arrow.svg");
  top: 13px;
  right: 13px;
}

.select-styled:active::after,
.select-styled.active::after {
  top: 13px;
  background-image: url("../img/icon-arrow.svg");
  transform: rotate(180deg);
}

.select-options {
  display: none;
  position: absolute;
  top: 100%;
  right: 0;
  left: 0;
  z-index: 999;
  margin: 0;
  padding: 0;
  list-style: none;
  box-sizing: border-box;
  background-color: #fff;
}

.select-options li {
  margin: 0;
  padding: 10px 0;
  text-indent: 15px;
  border-top: 1px solid #C4C4C4;
  transition: all 0.15s ease-in;
  border-right: 1px solid #C4C4C4;
  border-left: 1px solid #C4C4C4;
}

.select-options li:last-child {
  border-bottom: 1px solid #C4C4C4;
}

.select-options li:hover {
  color: #000;
  background: #fff;
}

.select-options li[rel="hide"] {
  display: none;
}

.calc__item--checkbox {
  width: 100%;
}

.calc__label--checkbox {
  font-size: 10px;
  line-height: 10px;
  position: relative;
  cursor: pointer;

  @media (min-width: $tablet-width) {
    padding-left: 44px;
    width: 100%;
  }

  &::before {
    @include pseudo;
    @include size(14px, 14px);
    box-sizing: border-box;
    border: 1px solid #fff;
    border-radius: 2px;
    top: 100%;
    margin-top: -17px;
    left: 45px;

    @media (min-width: $tablet-width) {
      left: 19px;
    }
  }
}

.calc__input--checkbox:checked + .calc__label--checkbox::before {
  background-image: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 8 8'%3e%3cpath fill='%23fff' d='M6.564.75l-3.59 3.612-1.538-1.55L0 4.26 2.974 7.25 8 2.193z'/%3e%3c/svg%3e");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: 70%;
}
