.request__form-link {
  color: $color-primary;
}

.form__input {
  font-family: $font-family-primary;
  font-size: 12px;
  line-height: 24px;
  color: $color-fourth;
  background: $background-color-fourth;
  border: 1px solid $border-color-tertiary;
  padding: 6px 9px 3px;
  width: 100%;

  &::placeholder {
    color: $color-fourth;
  }

  @media (min-width: $tablet-width) {
    width: 200px;
  }
}

.form__input--email {
  font-family: $font-family-primary;
  font-size: 12px;
  line-height: 24px;
  color: $color-fourth;
  background: $background-color-fourth;
  border: 1px solid $border-color-tertiary;
  padding: 6px 9px 3px;
  width: 100%;

  &::placeholder {
    color: $color-fourth;
  }

  @media (min-width: $tablet-width) {
    width: 200px;
  }
}

.form__input--checkbox  {
  width: 200px;
}

.form__item {
  margin-bottom: 10px;
  margin-top: 6px;
  position: relative;
}

.form__button {
  background-color: transparent;
  padding: 6px 9px 3px;
}

.form__button-text {
  color: $color-primary;
}

