.partners {

  @media (min-width: $desktop-width) {
    margin-top: 47px;
  }

  .title {
    //padding: 0 0 14px;
    text-align: left;

    @media (min-width: $tablet-width) {
      font-size: 24px;
      margin-bottom: 30px;
    }
  }
}
.partners__container {

  @media (min-width: $tablet-width) {
    padding: 0 10px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 111px;
    padding-right: 109px;
  }
}

.partners__list {
  @include ul-reset;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;

  @media (min-width: $tablet-width) {
    justify-content: center;
    width: 480px;
    margin: 0 auto;
  }

  @media (min-width: $desktop-width) {
    width: 100%;
    justify-content: space-evenly;
  }

