.partners__item {
  margin-bottom: 32px;

  @media (min-width: $tablet-width) {
    margin-bottom: 20px;
  }
}

.partners__image {
  &--apl {
    width: 71.18px;
    height: 39.38px;
  }

  &--maersk {
    width: 121.91px;
    height: 46.95px;
  }

  &--rzd {
    width: 73.45px;
    height: 32.56px;
  }

  &--fesco {
    width: 110.55px;
    height: 33.32px;
  }

  &--ttk {
    width: 96.92px;
    height: 40.89px;
  }
}
