.request {
  height: 682px;
  background-image: url("../img/bg-request-mobile@1x.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  padding-top: 182px;
  margin-top: -184px;
  color: $color-primary;
  z-index: 1;
  position: relative;

  @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
    background-image: url("../img/bg-request-mobile@2x.png");
  }

  @media (min-width: $tablet-width) {
    background-image: url("../img/bg-request-tablet@1x.png");
    height: 418px;
    padding-top: 171px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-request-tablet@2x.png");
    }
  }

  @media (min-width: $desktop-width) {
    background-image: url("../img/bg-request-desktop@1x.png");
    background-size: 1080px 330px;
    height: 330px;
    margin-top: -47px;
    padding-top: 159px;

    @media (min-resolution: $retina-dpi), (min-resolution: $retina-dppx) {
      background-image: url("../img/bg-request-desktop@2x.png");
    }
  }

  .title {
    padding: 0;
    margin: 0;
    text-align: left;

    color: $color-primary;
    background-image: none;
    background-clip: unset;

    @media (min-width: $tablet-width) {
      font-size: 24px;
      line-height: 30px;
    }
  }
}

.request__container {
  display: flex;
  flex-direction: column;

  @media (min-width: $tablet-width) {
    flex-direction: row;
    padding: 0 10px;
  }

  @media (min-width: $desktop-width) {
    padding-left: 111px;
    padding-right: 99px;
    align-items: flex-start;
  }
}

