.request__title {
  max-width: 132px;
}

.request__description {
  margin-top: 10px;
  margin-bottom: 12px;

  @media (min-width: $tablet-width) {
    width: 310px;
    margin-left: auto;
    margin-right: auto;
    margin-top: 0;
  }

  @media (min-width: $desktop-width) {
    margin-left: 33px;
    margin-right: 0;
  }
}

.request__form {
  display: flex;
  flex-direction: column;
  z-index: 1;

  @media (min-width: $tablet-width) {
    width: 200px;
  }

  @media (min-width: $desktop-width) {
    flex-direction: row;
  }
}

.request__form-list {
  @media (min-width: $desktop-width) {
    margin-left: 35px;
    display: flex;
    flex-direction: column;
    align-self: flex-end;
  }

}

.request__form-item {
  @media (min-width: $desktop-width) {
    margin-bottom: 15px;
    margin-top: 0;
  }
}

.request__form-button {
  width: 150px;
  margin: 0 auto;
  display: block;

  @media (min-width: $tablet-width) {
    width: 170px;
  }

  @media (min-width: $desktop-width) {
    margin-bottom: 18px;
  }
}



.request--inner {
  margin-top: -42px;

  @media (min-width: $tablet-width) {
    margin-top: 65px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 49px;
  }
}

.request--contacts {
  margin-top: -295px;
  margin-bottom: -11px;

  @media (min-width: $tablet-width) {
    margin-top: -92px;
    margin-bottom: 0;
  }

  @media (min-width: $desktop-width) {
    margin-top: -123px;
  }
}

.request--news {
  @media (min-width: $tablet-width) {
    margin-top: 38px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 145px;
  }
}

.request--services {

  @media (min-width: $tablet-width) {
    margin-top: 126px;
  }

  @media (min-width: $desktop-width) {
    margin-top: 159px;
  }
}

.request--index {
  margin-top: -126px;

  @media (min-width: $tablet-width) {
    margin-top: -6px;
  }

  @media (min-width: $desktop-width) {
    margin-top: -52px;
  }
}
