.social {

  @media (min-width: $tablet-width) {
    display: flex;
  }

  @media (min-width: $tablet-width) {
    //flex-direction: column;
  }
}

.social__header {
  font-size: 12px;
  line-height: 24px;
  color: #fff;
  padding: 1px 10px;
  background-image: url("../img/bg-data.svg");
  background-size: 100%;
  background-repeat: no-repeat;
  width: 100%;
  height: 26px;
  display: inline-block;
  margin-bottom: 12px;

  @media (min-width: $tablet-width) {
    width: 210px;
    margin-top: 4px;
  }

  @media (min-width: $desktop-width) {
    width: 216px;
  }
}

.ya-share2__item {
  @media (min-width: $desktop-width) {
    height: 39px;
    width: 39px;
  }
}
.social .ya-share2__container_mobile.ya-share2__container_size_l .ya-share2__icon {
  height: 39px;
  width: 39px;

  @media (min-width: $tablet-width) {
    height: 38px;
    width: 38px;
  }
}

.social .ya-share2__container_size_l .ya-share2__badge .ya-share2__icon {
  background-size: 39px 39px;
  background-repeat: no-repeat;
  background-position: center;

  @media (min-width: $tablet-width) {
    background-size: 38px 38px;
  }
}
