.slider__wrapper {
  overflow: hidden;
}

.slider__item {
  flex: 0 0 100%;
  max-width: 100%;
  //height: 407px;
  height: 300px;
  display: flex;
  justify-content: start;
  align-items: center;
  color: rgba(255, 255, 255, 1);
  padding-top: 0;

  @media (min-width: $tablet-width) {
    flex: 0 0 33.3333333333%;
    max-width: 33.3333333333%;
    padding-top: 40px;
  }
}

.slider__item--auto {
  @media (min-width: $tablet-width) {
    margin-top: 16px;
  }
}

.slider__control {
  background: none;
  width: 30px;
  height: 30px;
  bottom: 55px;
  z-index: 15;

  @media (min-width: $tablet-width) {
    bottom: 67px;
  }

  @media (min-width: $desktop-width) {
    bottom: 26px;
  }
}

.slider__indicators {
  bottom: 68px;

  @media (min-width: $tablet-width) {
    bottom: 80px;
    margin: 0;
  }

  @media (min-width: $desktop-width) {
    bottom: 39px;
  }
}

.slider__indicators li {
  margin-right: 1px;
  margin-left: 1px;
  height: 4px;
}

.slider__control[data-slide=prev]::before {
  background-image: url("../img/icon-slider-arrow.svg");
}

.slider__control[data-slide=next]::before {
  background-image: url("../img/icon-slider-arrow.svg");
  transform: rotate(180deg);
}

.slider__control[data-slide=next] {
  right: 20px;

  @media (min-width: $tablet-width)  {
    right: 0;
  }
}

.slider__control[data-slide=prev] {
  left: 20px;

  @media (min-width: $tablet-width) {
    left: 0;
  }
}

.slider__control:focus,
.slider__control:hover {
  background: none;
}

.slider__control[data-slide=prev]:focus::before,
.slider__control[data-slide=prev]:hover::before {
  background-image: url("../img/icon-slider-arrowwhite.svg");
  transform: rotate(180deg);
}

.slider__control[data-slide=next]:focus::before,
.slider__control[data-slide=next]:hover::before {
  background-image: url("../img/icon-slider-arrowwhite.svg");
  transform: rotate(0deg);
}



.ololo1 {
  width: 100%;
  display: flex;
  //outline: 1px solid red;
  justify-content: space-between;
  position: absolute;
  bottom: 45px;
  align-items: baseline;

  @media (min-width: $tablet-width) {
    width: 250px;
    right: 0;
    bottom: 0;
  }

  @media (min-width: $desktop-width) {
    right: 70px;
    bottom: -30px;
  }

}

.slider__control[data-slide=prev] {
  left: 0;
}

.slider__indicators {
  margin: 0;
}

.slider__control[data-slide=next] {
  right: 0;
}

.slider__indicators {
  position: relative;
  bottom: 0;

  @media (min-width: $tablet-width) {
    margin: 0 20px;
  }


}

.slider__control {
  position: relative;
  bottom: -13px;
}

.slider__control::before {
  width: 32px;
  height: 30px;
}
