.title {
  text-transform: uppercase;
  text-align: center;
  font-size: 24px;
  line-height: 30px;
  font-weight: 700;
  margin: 30px 0;

  color: transparent;
  -webkit-background-clip: text;
  background-clip: text;
  background-image: linear-gradient(to right,#4a84c5,#2d2e83);

  @media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    /* IE10+ specific styles go here */
    color: #2d2e83;
    background-color: transparent;
    background-image: none;
  }

  @media (min-width: $tablet-width) {
    font-size: 32px;
    line-height: 43px;
    margin-bottom: 60px;
  }
}

.title-container {
  background-image: linear-gradient(50deg, rgba(74,132,197,1) 0%, rgba(45,46,131,1) 100%);

  & > .title {
    background-color: white; /* Белый цвет фона */
    mix-blend-mode: screen; /* Режим наложения */
    color: rgba(45,46,131,1);
  }
}


